#notfound-page {
  width: 100%;
  height: 100%;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}
#notfound-page .container {
  width: 100%;
  height: 100%;
  padding: 0 15px;
}
#notfound-page .container .ant-row {
  width: 100%;
  height: 100%;
}
#notfound-page .container .ant-row .ant-col {
  align-self: center;
}
#notfound-page .notfound-left svg {
  width: 100%;
  visibility: hidden;
}
#notfound-page .notfound-right {
  font-family: "Nunito Sans";
  color: #000;
  font-size: 1em;
}
#notfound-page .notfound-right h1 {
  font-size: 7.5em;
  margin: 15px 0px;
  font-weight: bold;
}
#notfound-page .notfound-right h2 {
  font-weight: bold;
}
#notfound-page .notfound-right .btn {
  z-index: 1;
  overflow: hidden;
  background: transparent;
  position: relative;
  padding: 8px 50px;
  border-radius: 30px;
  cursor: pointer;
  font-size: 1em;
  transition: 0.2s ease;
  font-weight: bold;
  margin: 5px 0px;
  font-family: "Nunito Sans";
  border: 4px solid #317dda;
  color: #000;
}
#notfound-page .notfound-right .btn:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 0%;
  height: 100%;
  background: #317dda;
  z-index: -1;
  transition: 0.3s ease;
}
#notfound-page .notfound-right .btn:hover {
  color: #fff;
  background: #317dda;
  transition: 0.3s ease;
}
#notfound-page .notfound-right .btn:hover:before {
  width: 100%;
}
@media screen and (max-width: 768px) {
  body {
    display: block;
  }
  .container {
    margin-top: 70px;
    margin-bottom: 70px;
  }
}
@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}
